.admin-product-list {
    align-items: center;
    width: 50%;
    margin: auto;
    font-size: 16px;
}

.product-btn, .product-btn-delete {
    border-radius: 5px;
    margin: 5px 0px;
    padding: 10px;
    font-weight: 700;
    cursor: pointer;
    font-size: 18px;
    color: #fff;
    border: none;
}

.product-btn {
    background-color: #f08c42;
}

.product-btn-delete {
    background-color: #ff7f6a;
}

.admin-product-list button:hover {
    background-color: #a66320;
}

.admin-product-list ul {
    list-style-type: none;
    padding: 0;
}

.admin-product-list li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 10px 10px 15px;
    margin-bottom: 1.5px;
    border-radius: 5px;
    border: 1px solid #ddd;
}

.admin-product-list span {
    flex: 1;
    color: #444;
}

.admin-product-list button {
    margin-left: 10px;
}

@media (max-width: 600px) {
    .product-btn, .product-btn-delete {
        font-size: 14px;
        padding: 8px;
    }

    .admin-product-list {
        width: 90%;
    }
}


